














import Vue from 'vue';

export default Vue.extend({
  name: 'EndGame',
  props: ['count', 'total'],
  data(){
  	return{
  		cats:[] as any
  	}
  },
  methods:{
    play: function($event:Event){
      this.$router.push({name:'main', params:{playagain: 'true'}});
    }
  },
  created(){
  	if(!this.total){
  		this.$router.push('/');
  	}
  
  	for (var i=0; i<this.total; i++){
  		if(i<this.count){
  			this.cats.push('./img/cat-correct.png');
  		}else{
  			this.cats.push('./img/cat-incorrect.png');
  		}
  	}
  }
});
