















import Vue from 'vue';
import Importer from '../common/importer';
import QuizEvent from '../common/QuizEvent';
import QuizItem from '@/components/QuizItem.vue';
import Counter from '@/components/Counter.vue';
import AnswerChecker from '../common/answerChecker';



export default Vue.extend({
  name: 'Main',
  data (){
  	return{
      IMPORTER: Importer.getInstance(),
  		questions: [] as any[],
  		count: 0,
  		total: 0,
  		answerChecker: new AnswerChecker(),
      currentQuestionNum:0,
      prevHeight: 0,
      prevWidth:0,
      rect:{} as any,
      container: {} as any,
      audioCorrect: new Audio('./audio/meow_right.mp3'),
      audioIncorrect: new Audio('./audio/meow_sad_short.mp3')
    }
  },
  created(){
    this.loadGame();  
  },
  mounted(){
    this.audioCorrect.volume = 0.2;
    this.audioIncorrect.volume = 0.2;
    this.container = document.getElementById('container');


  
    


  },
  components: {
  	QuizItem,
  	Counter
  },
  methods:{
  	onInput: function($event:QuizEvent){
  		//if input received from counter
      var isCorrect:Boolean = this.answerChecker.checkAnswer($event.questionNumber, $event.answer);
      
      //stop prior audio
      if (!this.audioCorrect.paused){
        this.audioCorrect.pause();
        this.audioCorrect.currentTime = 0;
      }
      if (!this.audioIncorrect.paused){
        this.audioIncorrect.pause();
        this.audioIncorrect.currentTime = 0;
      };

      //play correct audio
  		if (isCorrect){
          this.audioCorrect.play();
  				this.count +=1;
  			}else{
          this.audioIncorrect.play();
        }
      
        (this.$refs.questionElements as any[])[0].setAnsweredView(isCorrect);
  		
  	},
    onNext: function($event:Event){
      this.currentQuestionNum++;
      if(this.currentQuestionNum == this.questions.length){
        this.endGame();
      }
    },
    loadGame: function(){
      var vue = this;
      this.IMPORTER.init(function(data:boolean|any[]){
          if(Array.isArray(data)){
            vue.questions = data;
            vue.total = data.length;
            vue.answerChecker.init(vue.IMPORTER.fetchAll());
            vue.count = 0;
          }
        }), false
    },
  	
    endGame: function(){
      this.$router.push({name:'end', params:{count: String(this.count), total: String(this.total)}});
    },
    beforeLeave(element:any) {
      this.prevHeight = this.rect.height;
      this.prevWidth = this.rect.width;


      this.rect = element.getBoundingClientRect();
      element.style.height = this.rect.height+'px';
      element.style.width = this.rect.width+'px';
      element.style.position = 'absolute';
       
    },
    beforeEnter(element:any){
     
    },
    enter(element:any) {
      
      this.rect = element.getBoundingClientRect();
      if(!this.prevHeight){
        this.prevHeight = this.rect.height;
        this.prevWidth = this.rect.width;
      }
      element.style.height = this.rect.height+'px';
      element.style.width = this.rect.width+'px';

      this.container.style.height = this.prevHeight+'px';
      this.container.style.width = Math.max(this.prevWidth, this.rect.width)+'px';
      element.style.position = 'absolute';
     

      element.style.height = this.prevHeight + 'px';
      


      setTimeout(() => {
        element.style.height = this.rect.height + 'px';
      });
    },
    afterEnter(element:any) {
        window.scrollTo({top:0, behavior:'smooth', left: 0})

        element.style.height = 'auto';
        element.style.width = 'auto';
        element.style.position = 'relative';
        this.container.style.width = 'auto';
        this.container.style.height = 'auto';
    }
  }
});


