import QuizItem from './QuizItem';

export default class AnswerChecker{
	
	private localData:any[] = [];

	constructor(){
		
	};

	public init(data:any[]){
		this.localData = data;
	}

	get total():number{
		return this.localData.length;
	}

	public checkAnswer(questionId:number, answer:string):Boolean{
		var j:number = 0;
		var quizItem:QuizItem = this.localData[questionId];
		if(quizItem.questionId==questionId){
			//check if answer correct in here and return result
			var answers:any[] = quizItem.answers;
			for (j = 0; j<answers.length; j++){
				if(answers[j].correct == answer){
					return true;
				}
			}
			return false;
		}
		return false;
	}
};
