import Papa from "papaparse";
import QuizItem from "./QuizItem";
import UTILS from "./utils";


export default class Importer {
	private static instance:Importer;

	private constructor(){
		
	}

	public static getInstance():Importer{
		if(!Importer.instance){
			Importer.instance = new Importer();
		}
		return Importer.instance;
	}
	private shuffledData:any[] = [];
	private originalData:any[] = [];
	private index:number = 0;
	private questionSetNum:number = 5;
	private loaded:boolean = false;

	private shuffle():void{

	}

	private process(results:any[]):void{
		
		const QUESTION:string = "question";
		const CORRECT_ANSWER:string = "correct";
		const FEEDBACK:string = "feedback";

		var header:any = results[0];
		var questionIndex:number = -1;
		var correctAnswerIndex:number = -1;
		var feedbackIndex:number = -1;
		
		this.originalData = [];
		this.shuffledData = [];
		/** set up indices for all non-wrong answers based on header row **/
		for (var val in header){
			if(header[val]==QUESTION){
				questionIndex = Number(val);
			}else if (header[val] == CORRECT_ANSWER){
				correctAnswerIndex = Number(val);
			}else if (header[val] == FEEDBACK){
				feedbackIndex = Number(val);
			}
		}

		//need to start on row 1 because row 0 is the header array
		for (var i=1; i<results.length; i++){
			var obj:QuizItem = new QuizItem(results[i], questionIndex, correctAnswerIndex, feedbackIndex);
			this.originalData.push(obj);
		}

		
		this.shuffledData = UTILS.shuffle(this.originalData);
		//TODO: reset shuffle each time you run out of items -- likely will end up with repeats
		// this.shuffledData = this.originalData;

		for (i=0; i<this.originalData.length; i++){
			this.originalData[i].questionId = i;
		}
	}

	private fetch():any[] {
		var tempArr:any[] = this.shuffledData.slice(this.index,this.index + this.questionSetNum);
		if(this.index + this.questionSetNum >= this.shuffledData.length){
			var appendNumber:number = this.index + this.questionSetNum - this.shuffledData.length;
			this.index = 0;
			tempArr = tempArr.concat(this.shuffledData.slice(this.index, this.index + appendNumber));
			this.index = (this.index + appendNumber)%this.shuffledData.length;
		}else{
			this.index=(this.index+this.questionSetNum)%this.shuffledData.length;
		}
		return tempArr;
	}

	public fetchAll():any[]{
		return this.originalData;
	}

	public init(onComplete:(data:any[]|boolean)=>void, loadOnly:boolean = false){
		if(this.loaded){
			return onComplete(this.fetch())
		}else{
			Papa.parse("./questions/quizshow.csv", {
				download:true, 
				header: false,
				error:function(error, file){
					throw new Error(error.message);
				},
				complete: (results) =>{
					this.loaded = true;
					this.process(results.data);
					if(!loadOnly){ 
						onComplete(this.fetch());
					}else{
						onComplete(true);
					}
				}
			});
		}
	}
}
