









import Vue from "vue";
import Importer from '@/common/importer';


export default Vue.extend({
  name: "Splash",
  data (){
    return{
      audio: new Audio('./audio/bat_transition.mp3'),
      playing: false,
      ready: false
    }
  },
  computed:{
   
  },
  created (){
    var vm = this;
    this.audio.onended = function (){
      vm.$router.push('main');
    }
      //initialize early and reuse in main
    const IMPORTER:Importer = Importer.getInstance();

    IMPORTER.init(function(data:boolean|any[]){
      if(data == true){
        vm.ready = true;
      }
    }, true);
  },
  mounted (){
     
  },
  methods:{
    play: function($event:Event){
      if(!this.playing){
        this.audio.play();
        this.playing = true
      }
    }
  }
});
