import UTILS from './utils';

export default class QuizItem{
	public answers:any[] = [];
	public question:string = '';
	public feedback:string = '';
	public questionId:Number = -1;

	
	

	private hash(answerString:String){
		var hashString:String[] = answerString.split('');
		return String(UTILS.shuffle(hashString).join('').replace(/ /g,'').substring(0,6) + Math.ceil(Math.random()*26543) + 20);
	}

	constructor(data:string[], questionIndex:Number, correctAnswerIndex:Number, feedbackIndex:Number){
			var correctAnswer:String = '';

			for (var j=0; j<data.length; j++){
				if(j == questionIndex){
					this.question = data[j];
				}else if(j == feedbackIndex){
					this.feedback = data[j];
				}else if (data[j].length>0){
					var hashVal = this.hash(data[j]);
					if (j == correctAnswerIndex){
						correctAnswer = hashVal;
					}else{
						correctAnswer = '';
					}

					this.answers.push({answer:data[j], val: hashVal, correct: correctAnswer})
				}
			}
			UTILS.shuffle(this.answers);
	};
};
