



















import Vue from 'vue';
import QuizEvent from '../common/QuizEvent';

export default Vue.extend({
  name: 'QuizItem',
  props: ['question', 'qindex'],
  data(){
    return{
      disabled: true as boolean,
      isAnswered: false as boolean,
      isCorrect: false as boolean
    }
  },
  methods:{
    onNext: function(){
      this.$emit('next');
    },
    setAnsweredView: function(correct:boolean){
      this.isAnswered = true;
      this.isCorrect = correct;
    }
  },
  mounted(){

    var $question:Element = (this as Vue).$el;
    
  },
  computed:{
  	inputListeners (this:any){
  		var vm = this;
  		return Object.assign({},
  			this.$listeners,{
  				input: ($event:QuizEvent)=>{
            vm.disabled = false;
            var target = $event.target as HTMLInputElement;
  					vm.$emit('input-event', {'answer':target.value, 'questionNumber': Number(String(target.name).split('-')[1])});
  				}
  			})
  	}
  }
});
